* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

#html {
  word-break: break-all;
}

#div1Wrapper {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  top: 20px;
}

#div1::before {
  content: "";
  display: block;
  position: absolute;
}

#div1::after {
  content: "";
  display: block;
  position: absolute;
}

@media (max-width: 500px) {
  #html {
    height: 50vh;
    overflow: auto;
  }

  #div1Wrapper {
    height: 50vh;
    position: relative;
  }

  #div1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@keyframes rotate {
  from{transform: rotate(0deg)}
  to{transform: rotate(359deg)}
}
